import {
	mapState,
	mapMutations
} from 'vuex';
import utils from '@/components/utils.filter.js';
export default {
	name: "taskItem",
	props: {
		dataList: {
			type: Array,
			default: () => []
		},
		scene: {
			type: String,
			default: 'task'
		},
		shopSelf: {
			type: Boolean,
			default: false
		}
	},
	data() {
		return {
			utils: utils,
			curScene: this.scene,
			curShopSelf: this.shopSelf
		};
	},
	computed: {
		...mapState(['globalOpenid', 'GlobalUserInfo', 'token', 'globalWXuserInfo', 'globalUserType'])
	},
	methods: {
		opendetailWork(e) {
			var that=this;
			if(that.GlobalUserInfo.id){
				uni.setStorageSync('taskDetail', e);
				uni.navigateTo({
					url: '/pages/home/taksDetail/taksDetail?scene=' + this.curScene
				});
			}else{
				uni.showModal({
				    title: '提示',
				    content: '您还未登录，请登录查看',
				    success: function (res) {
				        if (res.confirm) {
				          uni.navigateTo({
				          	url: '/pages/login/login'
				          });
				        } else if (res.cancel) {
				        }
				    }
				});
			}
			
		}
	}
}
